<template>
  <div class="document-main">
    <ul class="document-ul">
      <router-link to="/transition/" exact tag="li" active-class="document-ul-active">
        <a>1</a>
      </router-link>
      <router-link to="/transition/hoverto" tag="li" active-class="document-ul-active">
        <a>2</a>
      </router-link>
      <router-link to="/transition/tanxing" tag="li" active-class="document-ul-active">
        <a>3</a>
      </router-link>
      <router-link to="/transition/time" tag="li" active-class="document-ul-active">
        <a>4</a>
      </router-link>
  </ul>
  <transition mode="out-in">
    <router-view></router-view>
  </transition>
  </div>
</template>
<script>
export default {
  name: 'home'
}

</script>
<style>
.v-enter{
  opacity: 0;
}
.v-enter-to{
  opacity: 1;
}
.v-enter-active,.v-leave-active{
  transition: 1s;
}
.v-leave{
  opacity: 1;
}
.v-leave-to{
  opacity: 0;
}
</style>



